<template>
    <div class="textPage">
        <h3 class="doc">{{ text }}</h3>
        <iframe class="vid" v-bind:src="this.linkFromString" allowfullscreen="allowfullscreen"
        mozallowfullscreen="mozallowfullscreen" 
        msallowfullscreen="msallowfullscreen" 
        oallowfullscreen="oallowfullscreen" 
        webkitallowfullscreen="webkitallowfullscreen"></iframe>
        <!-- <iframe class="vid" src="inception"></iframe> -->
    </div>
</template>

<script>

export default {
    name: 'textPage',
    props: {
    text: String,
    link: String
    },
    computed: {
        linkFromString: function() {
            //console.log("http://youtube.com/embed/" + this.link)
            return "http://youtube.com/embed/" + this.link
        }
    }
}
</script>

<style>
    .vid {
        width: 75%;
        height: 35%;
    }

    .doc {
        color: black;
        font-family: monospace;
        margin: 2ch;
        line-height: 2;
    }
    .textPage {
        background-color:cornsilk;
        overflow: scroll;
    }
</style>
