<template>
  <div id="app">
    <h1 class="title">{{innerTitle}}</h1>
    <button class="nextPage" @click="prev()">Prev</button>
    <button class="nextPage" @click="next()">Next</button>
    <backgroundFolder :dualPage="this.projects[pageCount]"/>
    <inputBox v-on:submitted="title($event)"/>
  </div>
</template>

<script>
import backgroundFolder from './components/backgroundFolder.vue'
import inputBox from './components/inputBox.vue'
import projects from './data.js'


export default {
  name: 'App',

  components: {
    backgroundFolder,
    inputBox,
  },

  data: function() {
    return {
      innerTitle: "Beau's Portfolio",
      projects: projects,
      pageCount: 0,
    }
  },

  methods: {
    title: function (title) {
      this.innerTitle = title
      return title
    },
    prev: function () {
      if (this.pageCount <= 0) {
        this.pageCount = this.projects.length-1
      }
      else {
        this.pageCount--
      }
    },
    next: function () {
      if (this.pageCount >= this.projects.length-1) {
        this.pageCount = 0
      }
      else {
        this.pageCount++
      }
    },
  },
};
</script>

<style>
body {
  background-color: brown;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background-color: brown;
}
.title{
  font-family: monospace;
  color: cornsilk;
  margin-right: 5.5%;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}
.nextPage {
  left: 50%;
  right: 50%;
  background-color: cornsilk;
  border-radius: 20%;
  margin: 1%;
  margin-right: 6%;
}
</style>
