<template>
  <div class="inputBox">
    <input class="in" v-model="input"/>
    <button class="submit" @click="submit()">Submit</button>
  </div>
</template>

<script>

export default {
  name: 'inputBox',
  data: function () {
    return {
      input: ''
    }
  },
  methods: {
    submit: function () {
      this.$emit('submitted', this.input)
    }
  },
}
</script>

<style scoped>
  .submit {
    margin: 0.5%;
    border-radius: 4px;
    border-style: solid;
    border-color: rgba(188, 123, 43, 0.736);
    background-color: rgba(188, 123, 43, 0.736);
    margin-right: 4.5%;
  }
  .in {
    margin: 0.5%;
    border-radius: 4px;
    border-color: rgba(188, 123, 43, 0.736);
    border-style:solid
  }
</style>
